@family {
  font-family: 'Open Sans', 'Arial' sans-serif;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 0px;
  height: 0px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
}
.hidden-image {
  display: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
