}
.head-cell--hit::before {
  content: '';
  position: absolute;
  background: url(../img/hit-ribbon.png) no-repeat;
  background-size: 74px 73px;
  width: 74px;
  height: 73px;
  bottom: 20px;
  top: 0px;
  left: 0px;
}
.head-cell--hit::after {
  content: 'ХИТ';
  position: absolute;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 21px;
  text-transform: uppercase;
  font-weight: 300;
  top: 16px;
  left: 12px;
  transform: rotate(-45deg);
  color: #ffffff;
}
