  border: 2px solid #e5e5e5;
}
.table-row__cell--yes::after {
  content: '';
  position: absolute;
  background: url(../img/yes-svg.svg) no-repeat;
  background-size: 31px 21px;
  width: 31px;
  height: 21px;
  top: 15px;
  right: 15px;
}
.table-row__cell--no::after {
  content: '';
  position: absolute;
  background: url(../img/no-svg.svg) no-repeat;
  background-size: 27px 26px;
  width: 27px;
  height: 26px;
