  align-items: center;
  margin-top: 45px;
}
.slideshow-controls__dot {
  cursor: pointer;
  height: 6px;
  width: 6px;
  background-color: #283645;
  opacity: 0.3;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
  margin-right: 13px;
}
.slideshow-controls__dot:last-of-type {
