}
#price-radio-2:checked ~ .slideshow-container .slideshow-controls label[for=price-radio-2] {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
#price-radio-1:checked ~ .slideshow-container .slideshow-controls label[for=price-radio-1] {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
#price-radio-3:checked ~ .slideshow-container .slideshow-controls label[for=price-radio-3] {
  display: inline-block;
  border: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
#price-radio-1:checked ~ .slideshow-container .prices-table {
  transform: translateX(280px);
  transition-duration: 0.5s;
}
#price-radio-2:checked ~ .slideshow-container .prices-table {
  transition-duration: 0.5s;
}
#price-radio-3:checked ~ .slideshow-container .prices-table {
  transform: translateX(-280px);
  transition-duration: 0.5s;
}
